
.clock {
    position: relative;
    width: 200px;
    height: 200px;
    border: 10px solid #333;
    border-radius: 50%;
    margin: 50px auto;
  }
  
  .hand {
    position: absolute;
    width: 50%;
    height: 2px;
    background-color: red;
    top: 50%;
    left: 50%;
    transform-origin: 0% 50%;
  }
  
  .center {
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: #333;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .time {
    position: absolute;
    width: 100%;
    text-align: center;
    bottom: -30px;
    font-size: 24px;
  }